.hero {
  &__container {
    height: 100vh;
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  .hero_picture {
    width: 100%;
    height: 100%;

    cursor: pointer;
    transition: 1s all ease;

    z-index: 20;

    .carousel_slide {
      width: 100%;
      height: 100%;
      background: no-repeat url("./../pictures/main-pic.png");
      @include backgroundProperty;
      // transition: 0.8s all ease;
    }
  }

  .hero_about {
    background: no-repeat url("./../pictures/background.png");
    @include backgroundProperty;
    // background-color: green;

    &__container {
      width: 100%;
      height: 100vh;
      display: grid;
      grid-template-rows: 1fr 1fr auto;
      align-items: center;
    }

    .home_address {
      display: flex;
      justify-content: flex-end;
      margin-top: 4vw;

      &__container {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 75%;
        justify-content: center;
      }

      .heading {
        width: 100%;
      }

      .paragraph {
        opacity: 40%;
        margin-top: 1.5vw;

        p {
          width: 67%;
        }
      }
    }

    .home_button {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;

      &__container {
        align-self: flex-end;
        position: relative;

        .select_floor {
          cursor: pointer;

          // transition: all 2s ease;
          img {
            position: absolute;
            transform: translate(-50%, -50%);
            top: 50%;
            left: 20%;
            width: 16%;
          }

          .btn {
            color: $white;
            width: 35rem;
            height: 100%;
            background: $light-orange;
            box-shadow: $select-btn-shadow;
            border-radius: 14px 14px 0px 0px;
            padding: 3rem;
            display: block;
            text-align: center;
            text-decoration: none;

            p {
              transform: translateX(10%);
            }
          }
        }
      }
    }
  }

  .home_carusel {
    overflow: hidden;
    z-index: 5;
    position: relative;

    height: 90%;

    &__container {
      transform: translateX(-55%);

      z-index: 3;

      padding: 1vw 1vw 1.3vw 0vw;
      overflow: hidden;
      width: calc(45% * 6);

      .carusel {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: start;

        width: 100%;
        transition: 0.15s ease-in-out;

        .carusel_div {
          position: relative;
          margin-right: 2rem;
          border-radius: 10px;
          height: 10vw;

          width: 17vw;

          img {
            width: 100%;
            height: 100%;
            border-radius: 10px;
            position: absolute;
            border-radius: 10px;
            padding: 0.4rem;
            object-fit: cover;
          }
        }
      }
    }

    .about_room {
      width: 100%;
      display: flex;
      align-content: center;
      justify-content: center;
      align-items: center;
      z-index: -1;
      // height: 100%;
      position: relative;

      .container {
        background-color: white;
        display: flex;
        height: 100%;
        flex-direction: row;

        box-shadow: 0px 25px 60px -20px rgba(0, 0, 0, 0.2);
        width: 50%;
        border-radius: 14px;
        height: 7.4vw;
        position: absolute;
        justify-content: space-between;
        align-items: flex-end;
        top: -3vw;

        .paragraph {
          .display_none {
            display: none;
          }

          .display_block {
            display: block !important;
            transform: translateX(10%);
          }

          ul {
            display: flex;
            width: 100%;
            flex-direction: row;
            // height: 100%;
            overflow: hidden;

            transform: translateX(0%);

            li {
              list-style-type: none;

              height: 100%;
              margin-right: 1vw;
            }
          }
        }

        .btn {
          margin-bottom: -0.5vw;
          margin-right: 10%;
          display: flex;
          align-items: center;

          .prev,
          .next {
            img {
              cursor: pointer;
              width: 3.5rem;
              //  z-index: 9;
            }
          }

          .next,
          .prev {
            cursor: pointer;
            // margin-left: -3rem;
            z-index: 9;
          }
        }
      }
    }
  }
}

main {
  .container {
    height: 100vh;
    display: grid;
    grid-template-columns: 2.2fr 1fr;
    // margin-top: 20rem;
  }

  .building_section {
    position: relative;
    width: 100%;
    height: 100%;

    &__model {
      width: 100%;
      height: 100%;
      // background: no-repeat url("./../pictures/building.png");
      @include backgroundProperty;
    }

    &__btn {
      position: absolute;
      transform: translate(-50%, -50%);
      top: 18%;
      left: 9%;
      width: 4vw;
      height: 4vw;
      background-color: white;
      border-radius: 14px;
      display: flex;
      justify-content: center;
      align-items: center;
      box-shadow: -8px -8px 24px rgba(255, 255, 255, 0.94),
        8px 8px 20px rgba(0, 0, 0, 0.22);
      cursor: pointer;
    }
  }

  .work_section {
    display: grid;
    grid-template-rows: 2fr 1fr;

    .introduction {
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;

      &:after {
        content: "";
        transform: translate(-50%, -50%);
        position: absolute;
        width: 100%;
        height: 100%;
        background: no-repeat url("./../pictures/circle.png");
        @include backgroundProperty;
        top: 39%;
        left: 50%;
        z-index: -1;
      }
    }

    &__container {
      width: 70%;

      .paragraph {
        margin-top: 2vw;
        opacity: 60%;
      }
    }

    &__gif {
      background-color: #ffa800;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;

      .yellow {
        width: 100%;
        height: 100%;
        background-color: #ffa800;
        transform: translateY(-3%);
        filter: blur(20px);
        filter: contrast(50px);
        opacity: 0.5;
        position: absolute;
        transform: translate(-50%, -50%);
        top: 46%;
        left: 50%;
        z-index: -1;
      }

      .video {
        background: url("./../pictures/comp-1.gif");
        @include backgroundProperty;
        width: 50%;
        height: 80%;
      }
    }
  }

  .about {
    height: 100vh;
    margin-top: 4rem;

    .main_container {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;

      .side_section {
        position: absolute;
        transform: translate(-50%, -50%) rotateZ(-90deg);
        width: 40rem;
        // height: 100%;
        left: 3%;
        top: 56%;

        &__container {
          width: 100%;
          height: 100%;

          ul {
            list-style-type: none;
            display: flex;
            justify-content: space-between;
            // align-items: center;
            flex-direction: row;

            a {
              color: $dark;
              text-decoration: none;
            }
          }
        }
      }
    }

    .wrapper {
      width: 75%;
      height: 100%;
      display: grid;
      grid-template-rows: 0.9fr 1fr;
    }

    .main_text {
      position: relative;
      background-color: gainsboro;
      display: flex;
      flex-direction: column;
      // align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;
      background: no-repeat url("./../pictures/number_one.png");
      background-size: 22%;
      background-position: 0% 17%;
      object-fit: cover;

      .heading {
        margin-bottom: 3vw;
      }

      .paragraph {
        width: 62%;
      }
    }

    .apartaments_section {
      // background-color: beige;
      display: grid;
      grid-template-columns: auto 1fr;
      border-top: 1px solid $light-orange;

      .about_apartaments {
        &__container {
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          // width: 100%;
          height: 100%;
          width: 14.5vw;
        }
      }

      .room_section {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
      }

      .bedroom {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;

        .picture {
          // margin: 0 auto;
          // width: 100%;
          // height: 100%;
          // background: no-repeat url("./../pictures/bedroom.png");
          // background-size: cover;
          // background-position: center;
          // object-fit: cover;
          align-self: center;

          padding: 1.5vw 0vw 0vw 0vw;
          margin-left: 4vw;
          margin-top: 1rem;

          img {
            width: 95%;
            height: 100%;
            border-radius: $border-radius;

            box-shadow: $box-shadow-for-cards;
          }
        }

        .about_room {
          width: 100%;
          // height: 100%;
          display: flex;
          flex-direction: column;
          justify-content: space-evenly;
          margin-left: 4.5vw;
          margin-top: 0.7vw;

          &__number {
            opacity: 60%;
            margin-bottom: 1rem;
          }
        }
      }
    }
  }





}